import React, { Component } from "react";
// eslint-disable-next-line import/no-cycle
import { Typography, Button } from "components";
import UserContext from "containers/App/UserContext";
import Loader from "./Loader";

import "./index.css";

export default class Deposit extends Component {
  static contextType = UserContext;

  state = {
    amount: 0,
    onDeposit: null
  };

  handleClick = () => {
    this.setState({ onDeposit: "processing" });

    // replace setTimeout by API call
    setTimeout(() => {
      this.setState({ onDeposit: "completed" });
    }, 3000);
  };

  handleTokens = () => {
    // replace setTimeout by API call
    setTimeout(() => {
      this.setState({ amount: 10 });
    }, 2000);
  };

  render() {
    const { amount, onDeposit } = this.state;

    return (
      <div styleName="deposit">
        <div styleName="title">
          <a
            href="https://uniswap.exchange/swap"
            target="_blank"
            rel="noopener noreferrer"
            onClick={this.handleTokens}
          >
            <Typography variant="h4" color="white">
              Do you have Tokens?
            </Typography>
          </a>
        </div>

        <div styleName="button">
          {!onDeposit ? (
            <Button
              disabled={amount <= 0 || onDeposit}
              name="deposit"
              theme="primary"
              variant="small-body"
              onClick={this.handleClick}
            >
              <Typography>Deposit</Typography>
            </Button>
          ) : null}
          {onDeposit === "processing" ? <Loader /> : null}
          {onDeposit === "completed" ? (
            <Typography color="green" variant="body">
              Deposit completed!
            </Typography>
          ) : null}
        </div>
      </div>
    );
  }
}
