import { enableMetamask, getMetamaksAccount, getNonce } from "lib/metamask";
import CasinoContract from "lib/ethereum/CasinoContract";
import {
  updateUserWallet,
  requestWithdraw,
  finalizeWithdraw,
  createBet
} from "lib/api/users";
import CryptographySingleton from "lib/api/Cryptography";

export default class User {
  constructor({
    userId,
    platformAddress,
    tokenAddress,
    decimals,
    bearerToken,
    appId
  }) {
    this.user_id = userId;
    this.app_id = appId;
    this.platformAddress = platformAddress;
    this.tokenAddress = tokenAddress;
    this.decimals = decimals;
    this.bearerToken = bearerToken;
  }

  /**
   * @use Initialization Function
   */

  __init__() {
    this.setupCasinoContract();
  }

  setupCasinoContract() {
    /* Create Casino Instance */
    this.casinoContract = new CasinoContract({
      web3: window.web3,
      contractAddress: this.platformAddress,
      tokenAddress: this.tokenAddress,
      decimals: this.decimals
    });
  }

  createDeposit = async ({ amount }) => {
    try {
      /* Enable Metamask Auth */
      await enableMetamask("eth");

      /* Deposit Tokens */
      const resEthereum = await this.casinoContract.depositTokens({
        amount
      });

      /* Update API Wallet Update */
      await updateUserWallet(
        {
          user: this.user_id,
          amount,
          app: this.app_id,
          transactionHash: resEthereum.transactionHash
        },
        this.bearerToken
      );

      return true;
    } catch (err) {
      // TO DO : Verify if User declined Metamask or there was another type of error
      // TO DO : Display the Error
      throw err;
    }
  };

  withdrawTokens = async ({ amount }) => {
    try {
      /* Enable Metamask Auth */
      await enableMetamask("eth");

      /* Get Metamask Account Address */
      const accAddress = await getMetamaksAccount();

      /* Ask Permission to Withdraw */
      const resAPIRequestWithdraw = await requestWithdraw(
        {
          app: this.app_id,
          user: this.user_id,
          address: accAddress,
          tokenAmount: amount,
          nonce: getNonce()
        },
        this.bearerToken
      );

      /* Run Withdraw Function */
      const resEthereum = await this.casinoContract.withdrawTokens({
        address: accAddress,
        amount
      });

      /* Finalize Withdraw to API */
      const resAPIFinalizeWithdraw = await finalizeWithdraw(
        {
          app: this.app_id,
          user: this.user_id,
          address: accAddress,
          tokenAmount: amount,
          transactionHash: resEthereum.transactionHash,
          nonce: getNonce()
        },
        this.bearerToken
      );
    } catch (err) {
      throw err;
    }
  };

  createBet = async ({ amount, result, gameId }) => {
    try {
      /* Enable Metamask Auth */
      await enableMetamask("eth");

      /* Get Metamask Account Address */
      const accAddress = await getMetamaksAccount();

      /* Generate User Signature */
      const { signature, nonce } = await CryptographySingleton.getUserSignature(
        {
          address: accAddress,
          nonce: getNonce(),
          winBalance: amount,
          decimals: this.decimals,
          result,
          category: 1
        }
      );

      /* Create Bet API Setup */
      return await createBet(
        {
          user: this.user_id,
          app: this.app_id,
          game: gameId,
          result,
          address: accAddress,
          signature,
          nonce
        },
        this.bearerToken
      );
    } catch (err) {
      throw err;
    }
  };
}
