import React, { Component } from "react";
// eslint-disable-next-line import/no-cycle
import { Typography, Button, SubtleButton } from "components";
import UserContext from "containers/App/UserContext";
import { isNull } from "lodash";
import Loader from "./Loader";

import "./index.css";

export default class Withdraw extends Component {
  static contextType = UserContext;

  state = {
    permission: false,
    processing: null
  };

  handleWithdraw = () => {
    // replace setTimeout by API calls

    setTimeout(() => {
      this.setState({ processing: true }, () => {
        setTimeout(() => {
          this.setState({ processing: false });
        }, 2000);
      });
    }, 100);
  };

  handlePermission = () => {
    // replace setTimeout by API call
    setTimeout(() => {
      this.setState({ permission: true });
    }, 500);
  };

  render() {
    const { permission, processing } = this.state;

    return (
      <div styleName="deposit">
        <div styleName="title">
          <SubtleButton
            variant="h4"
            name="permission"
            onClick={this.handlePermission}
          >
            Ask permission
          </SubtleButton>
        </div>
        <div styleName="button">
          {isNull(processing) ? (
            <Button
              disabled={permission !== true}
              name="withdraw"
              theme="primary"
              onClick={this.handleWithdraw}
            >
              <Typography>Withdraw</Typography>
            </Button>
          ) : null}
          {processing ? <Loader /> : null}
          {!isNull(processing) && !processing ? (
            <Typography color="green" variant="body">
              Withdraw completed!
            </Typography>
          ) : null}
        </div>
      </div>
    );
  }
}
