import axios from "axios";
import handleError from "./handleError";
import { apiUrl, appId } from "./apiConfig";

export async function register({ username, password, email, address }) {
  try {
    const response = await axios.post(`${apiUrl}/users/register`, {
      username,
      email,
      password,
      name: username,
      app: appId,
      address: address || "Porto"
    });

    if (response.data.data.status !== 200) {
      return response.data.data;
    }

    const { status, message } = response.data.data;

    return {
      status,
      balance: message.wallet.playBalance,
      // eslint-disable-next-line no-underscore-dangle
      id: message._id,
      username: message.username
    };
  } catch (error) {
    return handleError(error);
  }
}

export async function login({ username, password }) {
  try {
    const response = await axios.post(`${apiUrl}/users/login`, {
      username,
      password
    });

    if (response.data.data.status !== 200) {
      return response.data.data;
    }

    const { status, message } = response.data.data;

    return {
      status,
      balance: message.wallet.playBalance,
      id: message.id,
      username: message.username
    };
  } catch (error) {
    return handleError(error);
  }
}

export async function getCurrentUser() {
  try {
    return JSON.parse(localStorage.getItem("user"));
  } catch (error) {
    return handleError(error);
  }
}

export async function updateUserBalance(user, setUser) {
  try {
    const response = await axios.post(`${apiUrl}/users/summary`, {
      user: user.id,
      type: "WALLET"
    });

    const newUser = {
      ...user,
      balance: response.data.data.message["0"].playBalance
    };

    return setUser(newUser);
  } catch (error) {
    return handleError(error);
  }
}

/**
 *
 * @param {*} params
 * @param {*} bearerToken
 * @name Update Wallet
 * @use After Deposit Being Done Decentralized
 */

export async function updateUserWallet(
  { user, amount, app, transationHash },
  bearerToken
) {
  try {
    return axios
      .post(
        `${apiUrl}/users/updateWallet`,
        {
          user,
          amount,
          app,
          transationHash
        },
        addSecurityHeader(bearerToken)
      )
      .then(res => {
        return res.data;
      })
      .catch(error => {
        throw error;
      });
  } catch (error) {
    return handleError(error);
  }
}

export async function logout() {
  localStorage.removeItem("user");
  sessionStorage.clear();
}

/**
 *
 * @param {*} params
 * @param {*} bearerToken
 * @name Request Withdraw
 * @use Once User Wants to Withdraw Decentralized
 */

export async function requestWithdraw(params, bearerToken) {
  return axios
    .post(
      `${apiUrl}/users/requestWithdraw`,
      params,
      addSecurityHeader(bearerToken)
    )
    .then(res => {
      return res.data;
    })
    .catch(error => {
      throw error;
    });
}

/**
 *
 * @param {*} params
 * @param {*} bearerToken
 * @name Finalize Withdraw
 * @use Once User has Finalized or just canceled his Withdraw
 */

export async function finalizeWithdraw(params, bearerToken) {
  return axios
    .post(
      `${apiUrl}/users/finalizeWithdraw`,
      params,
      addSecurityHeader(bearerToken)
    )
    .then(res => {
      return res.data;
    })
    .catch(error => {
      throw error;
    });
}

/**
 *
 * @param {*} params
 * @param {*} bearerToken
 * @name Create Bet
 * @use Once User wants to bet
 */

export async function createBet(params, bearerToken) {
  return axios
    .post(
      `${apiUrl}/app/games/bet/place`,
      params,
      addSecurityHeader(bearerToken)
    )
    .then(res => {
      return res.data;
    })
    .catch(error => {
      throw error;
    });
}

function addSecurityHeader(bearerToken) {
  return {
    headers: {
      authorization: `Bearer ${bearerToken}`
    }
  };
}
