import SHA512 from "crypto-js/hmac-sha512";
import SHA512_HASH from "crypto-js/sha512";
import randomstring from "randomstring";
import randomHex from "randomhex";
import Web3 from "web3";
import { Numbers } from "../ethereum/lib";

class Cryptography {
  generateSeed(length = 32) {
    return randomHex(length);
  }

  hashSeed(seed) {
    return SHA512_HASH(seed);
  }

  generateNonce() {
    return Math.floor(Math.random() * 10000000000000000) + 1;
  }

  generateRandomResult(serverSeed, clientSeed) {
    return SHA512(serverSeed, clientSeed);
  }

  generatePrivateKey = () => {
    return `0x${randomstring.generate({
      charset: "hex",
      length: 64
    })}`;
  };

  getUserSignature = async ({
    address,
    winBalance,
    nonce,
    category,
    decimals
  }) => {
    /*
    
    const message = await window.web3.utils.soliditySha3(
      {
        type: "int128",
        value: Numbers.fromExponential(
          Numbers.toSmartContractDecimals(winBalance, decimals)
        )
      },
      { type: "uint128", value: nonce },
      { type: "uint8", value: category }
    );

    const response = await window.web3.eth.sign(message, address);

    */

    return {
      signature: {
        v: "0x1c",
        r: "0x33c3a35f8e579224c25ce4436a5b519b5f78e01e4714e50606dd54a02bcffd58",
        s: "0x1d5230d5f07b4c553ccf39a607e27fc3e1c9a94020de33354c4163681a6cb362"
      },
      nonce,
      category,
      address
    };
  };
}

const CryptographySingleton = new Cryptography();

export default CryptographySingleton;
