import React, { Component } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { Button, SubtleButton, Typography, UserMenu } from "components";
import UserContext from "containers/App/UserContext";
import Bitcoin from "components/Icons/Bitcoin";
import logo from "../../assets/logo.ico";

import "./index.css";

export default class Navbar extends Component {
  static contextType = UserContext;

  static propTypes = {
    user: PropTypes.shape({}),
    onLoginRegister: PropTypes.func.isRequired,
    onLogout: PropTypes.func.isRequired,
    onCashier: PropTypes.func.isRequired
  };

  static defaultProps = {
    user: null
  };

  constructor(props) {
    super(props);
    this.state = {
      user: props.user
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    return {
      user: nextProps.user,
      difference:
        nextProps.user && prevState.user
          ? nextProps.user.balance - prevState.user.balance
          : null
    };
  }

  handleClick = event => {
    const { onLoginRegister } = this.props;

    if (onLoginRegister) onLoginRegister(event.currentTarget.name);
  };

  render() {
    const { onLogout, onCashier } = this.props;
    const { user, difference } = this.state;

    return (
      <div styleName="root">
        <div styleName="container">
          <Link to="/">
            <img styleName="image" alt="bet protocol logo" src={logo} />
          </Link>

          {user ? (
            <div styleName="content">
              <div styleName="coin">
                <Typography color="white">
                  {user.balance ? user.balance.toFixed(6) : 0}
                </Typography>
                <div styleName="icon">
                  <Bitcoin />
                </div>
                {difference ? (
                  <div
                    key={user.balance}
                    styleName={difference > 0 ? "diff-won" : "diff-lost"}
                  >
                    <Typography variant="small-body">
                      {Math.abs(difference).toFixed(6)}
                    </Typography>
                  </div>
                ) : null}
              </div>
              <div styleName="buttons">
                <UserMenu
                  onLogout={onLogout}
                  onCashier={onCashier}
                  username={user.username}
                />
              </div>
            </div>
          ) : (
            <div styleName="buttons">
              <div styleName="login">
                <SubtleButton onClick={this.handleClick} name="login">
                  Login
                </SubtleButton>
              </div>
              <Button size="x-small" onClick={this.handleClick} name="register">
                <Typography color="white">Register</Typography>
              </Button>
            </div>
          )}
        </div>
      </div>
    );
  }
}
